import Blog from "@/models/blog.model";
import databaseConnection from "@/database/database";
import { NextResponse } from "next/server";
import cloudinary from "@/helpers/cloudinary";

databaseConnection();

export async function POST(request) {
  try {
    // const reqFiles = await request.json();

    // const { thumbnail } = request.files;
    // if (!req.files || Object.keys(req.files).length === 0) {
    //   return next(new ErrorHandler("Blog Thumbnail Is Required", 400));
    // }

    // const cloudinaryResponse = await cloudinary.v2.uploader.upload(
    //   thumbnail.tempFilePath,
    //   { folder: "Blog Thumbnails" }
    // );

    // if (!cloudinaryResponse || cloudinaryResponse.error) {
    //   console.error(
    //     "Cloudinary Error:",
    //     cloudinaryResponse.error || "Unknown Cloudinary Error"
    //   );
    //   return next(new ErrorHandler("Failed To Upload Thumbnail", 500));
    // }

    const category = await Category.findById(req.body.category);
    if (!category) {
      return next(new ErrorHandler("Category Not Found", 404));
    }

    const reqBody = await request.json();

    const {
      title,
      description,
      slug,
      canonical,
      alternateText,
      intro,
      content,
      metaTitle,
      metaDescription,
      primaryKeyword,
      faq,
      tableOfContent,
    } = reqBody;

    let parsedIntro;
    try {
      parsedIntro = JSON.parse(intro);
    } catch (error) {
      return next(new ErrorHandler("Invalid Intro format", 400));
    }

    let parsedContent;
    try {
      parsedContent = JSON.parse(content);
    } catch (error) {
      return next(new ErrorHandler("Invalid content format", 400));
    }

    const isSlugExists = await Blog.findOne({ slug });
    if (isSlugExists) {
      return next(new ErrorHandler("Blog With This Slug Already Exists", 400));
    }

    const newSlug = slug.toLowerCase().replace(/ /g, "-");

    let parsedFaq = [];
    try {
      parsedFaq = JSON.parse(faq);
    } catch (error) {
      return next(new ErrorHandler("Invalid FAQ Format", 400));
    }

    let parsedTableOfContent = [];
    try {
      parsedTableOfContent = JSON.parse(tableOfContent);
    } catch (error) {
      return next(new ErrorHandler("Invalid FAQ Format", 400));
    }

    const blog = await Blog.create({
      title,
      description,
      slug: newSlug,
      canonical,
      category: category._id,
    //   thumbnail: {
    //     public_id: cloudinaryResponse.public_id,
    //     url: cloudinaryResponse.secure_url,
    //   },
      alternateText,
      intro: parsedIntro,
      content: parsedContent,
      metaTitle,
      metaDescription,
      primaryKeyword,
      tableOfContent: parsedTableOfContent,
      faq: parsedFaq,
      user: req.user._id,
    });

    return NextResponse.json(
      {
        success: true,
        message: "Blog Created Successfully",
        blog,
      },
      {
        status: 200,
      }
    );
  } catch (error) {
    console.error("Some Error Occured While Creating New Blogs", error);
    return NextResponse.json(
      {
        success: false,
        message: "Blogs Creation Failed",
      },
      {
        status: 500,
      }
    );
  }
}
